import React from 'react'
import firstAvatar from '../assets/avatars-png/Hunny.png'
import secondAvatar from '../assets/avatars-png/AppleSpice.png'
import thirdAvatar from '../assets/avatars-png/Skittlebun.png'
import fourthAvatar from '../assets/avatars-png/Puddytat.png'
import fifthAvatar from '../assets/avatars-png/Shugapop.png'
import sixthAvatar from '../assets/avatars-png/Onyx.png'
import seventhAvatar from '../assets/avatars-png/Hunny.png'
import eightAvatar from '../assets/avatars-png/Hunny.png'
import ScrollAnimation from 'react-animate-on-scroll';
import './ThirdAvatar.css';

const ThirdAvatar = () => {
  return (
    <>
      <div className="third-avatar">
        <ScrollAnimation animateIn='fadeIn'>
        <h1 className='third-title'>BABYSITTERS</h1>
        </ScrollAnimation>
        <div className="avatars-3">
            <ScrollAnimation animateIn='fadeIn' delay={400}>
            <div className='avatar-3'>
                <img src={firstAvatar} alt="HUNNYXXBOO"/>
                <h3>HUNNYXXBOO</h3>
            </div>
            </ScrollAnimation>
            <ScrollAnimation animateIn='fadeIn' delay={400}>
            <div className='avatar-3'>
                <img src={secondAvatar} alt="APPLESPICE"/>
                <h3>APPLESPICE</h3>
            </div>
            </ScrollAnimation>
            <ScrollAnimation animateIn='fadeIn' delay={400}>
            <div className='avatar-3'>
                <img src={thirdAvatar} alt="SKITTLEBUN"/>
                <h3>SKITTLEBUN</h3>
            </div>
            </ScrollAnimation>
            <ScrollAnimation animateIn='fadeIn' delay={400}>
            <div className='avatar-3'>
                <img src={fourthAvatar} alt="PUDDYTAT"/>
                <h3>PUDDYTAT</h3>
            </div>
            </ScrollAnimation>
            <ScrollAnimation animateIn='fadeIn' delay={400}>
            <div className='avatar-3'>
                <img src={fifthAvatar} alt="SHUGAPOP"/>
                <h3>SHUGAPOP</h3>
            </div>
            </ScrollAnimation>
            <ScrollAnimation animateIn='fadeIn' delay={400}>
            <div className='avatar-3'>
                <img src={sixthAvatar} alt="ONYX"/>
                <h3>ONYX</h3>
            </div>
            </ScrollAnimation>
        </div>
        <div className='mobile-avatars-3'>
            <div id="carouselExampleSlidesOnly" class="carousel slide" data-ride="carousel">
            <div className="carousel-inner">
                    <div className="carousel-item active">
                        <img className="d-block w-100" src={firstAvatar} alt="First slide"></img>
                        <div className="carousel-caption d-block d-md-block">
                            <h5>HUNNYXXBOO</h5>
                        </div>
                    </div>
                    <div className="carousel-item">
                        <img className="d-block w-100" src={secondAvatar} alt="Second slide"></img>
                        <div className="carousel-caption d-block d-md-block">
                            <h5>APPLESPLICE</h5>
                        </div>
                    </div>
                    <div className="carousel-item">
                        <img className="d-block w-100" src={thirdAvatar} alt="Third slide"></img>
                        <div className="carousel-caption d-block d-md-block">
                            <h5>SKITTLEBUN</h5>
                        </div>
                    </div>
                    <div className="carousel-item">
                        <img className="d-block w-100" src={fourthAvatar} alt="Fourth slide"></img>
                        <div className="carousel-caption d-block d-md-block">
                            <h5>PUDDYTAT</h5>
                        </div>
                    </div>
                    <div className="carousel-item">
                        <img className="d-block w-100" src={fifthAvatar} alt="Fifth slide"></img>
                        <div className="carousel-caption d-block d-md-block">
                            <h5>SHUGAPOP</h5>
                        </div>
                    </div>
                    <div className="carousel-item">
                        <img className="d-block w-100" src={sixthAvatar} alt="Sixth slide"></img>
                        <div className="carousel-caption d-block d-md-block">
                            <h5>ONYX</h5>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <ScrollAnimation animateIn='fadeIn'>
        <h1 className='third-title' id='baby-makers'>BABYMAKERS</h1>
        </ScrollAnimation>
        <div>
            <div className='makers-avatar-all'>
                <div className='makers-avatar' id='left-makers'>
                    <ScrollAnimation animateIn='fadeIn' delay={400}>
                    <div className='makers-text'>
                        <h3 className='heads'>MOHAWKEYE</h3>
                        <h3 className='sub-head'>THE VANDAL</h3>
                        <h5 className='desc'>ADD DESCRIPTION HERE</h5>
                        <h5>ADD DESCRIPTION HERE</h5>
                        <h5>ADD DESCRIPTION HERE</h5>
                    </div>
                    </ScrollAnimation>
                    <ScrollAnimation animateIn='fadeIn' delay={200}>
                    <img src={seventhAvatar} alt="MOHAWKEYE" />
                    </ScrollAnimation>
                </div>
                <div className='makers-avatar' id='right-makers'>
                    <ScrollAnimation animateIn='fadeIn' delay={400}>
                    <div className='makers-text'>
                        <h3 className='heads'>MOHAWKEYE</h3>
                        <h3 className='sub-head'>THE VANDAL</h3>
                        <h5 className='desc'>ADD DESCRIPTION HERE</h5>
                        <h5>ADD DESCRIPTION HERE</h5>
                        <h5>ADD DESCRIPTION HERE</h5>
                    </div>
                    </ScrollAnimation>
                    <ScrollAnimation animateIn='fadeIn' delay={200}>
                    <img src={seventhAvatar} alt="MOHAWKEYE" />
                    </ScrollAnimation>
                </div>
            </div>
            
        </div>
      </div>

    </>
  )
}

export default ThirdAvatar
