import './Home.css';
import Hero from '../Hero';
import Merch from '../Merch';
import Avatars from '../Avatars';
import React from 'react';
import Roadmap from '../Roadmap';
import SecondAvatar from '../SecondAvatar';
import ThirdAvatar from '../ThirdAvatar';
import Fact from '../Fact';
import FourthAvatar from '../FourthAvatar';
import Footer from '../Footer';
const Home = () => {
  return (
    <> 
      <div className='hero-section' id='hero-section'>
        <Hero />
      </div>
      <div className='merch-section' id='merch-section'>
        <Merch />
      </div>
      <div className="avatar-section" id='avatar-section'>
        <Avatars />
      </div>
      <div className="roadmap" id='roadmap=section'>
        <Roadmap />
      </div>
      <div className="fact">
        <Fact />
      </div>
      <div className="second-avatar">
        <SecondAvatar />
      </div>
      <div className="third-avatar">
        <ThirdAvatar />
        <FourthAvatar />
      </div>
      <div className='footer'>
        <Footer/>
      </div>
    </>
  )
}

export default Home
